.side-bar {
  margin: 0;
  min-height: 69px;
  padding: 0 10px 10px 10px;
  position: relative;
  text-shadow: 0 1px 1px #000;
  transition: all 0.3s linear 0s;
}
@media only screen and (max-width: 768px) {
  .side-bar {
    text-align: center;
  }
}
.side-bar h3 {
  color: #fff;
  font-size: 22px;
  font-family: "Bangers", cursive;
  letter-spacing: 1px;
  margin: 0 0 15px 0;
}
.side-bar ul {
  margin: 0;
  padding: 0;
}
.side-bar ul li {
  background: rgba(0, 0, 0, 0.4);
  box-shadow: 0px 0px 10px -4px #000;
  display: block;
  font-size: 16px;
  margin: 0 0 10px 0;
  padding: 10px;
}
.side-bar ul li p:first-of-type {
  text-transform: capitalize;
}
.side-bar ul li p:last-of-type {
  cursor: pointer;
  font-size: 20px;
}
.side-bar ul li:last-of-type {
  margin: 0;
}
.side-bar ul li p {
  cursor: pointer;
  color: #fff;
}
.sidebar-item {
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.side-bar ul li button {
  background: #f45050;
  border: 1px solid #7a2929;
  border-radius: 2px;
  box-shadow: 0px 0px 10px -4px #7a2929;
  color: #fff;
  cursor: pointer;
  text-shadow: 0 1px 1px #7a2929;
}
.sidebar-spinner-wrap {
  align-items: center;
  display: none;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  position: relative;
  right: 0;
  top: 0;
  width: 90%;
  z-index: 9;
}
.sidebar-spinner-wrap.show {
  display: flex;
  margin: 42px 0 0 0;
}
.sidebar-spinner-wrap p {
  color: #fff;
  font-family: "Bangers", cursive;
  font-size: 14px;
  margin: 0 0 5px 0;
}
.sidebar-spinner-wrap .sidebar-spinner {
  background-color: #290e0e;
}
.sidebarUnactive {
  transform: translateY(-10vh);
}
.list-item-appear {
  opacity: 0.01;
}
.list-item-appear.list-item-appear-active {
  opacity: 1;
  transition: opacity 0.5s ease-in;
}
.list-item-enter {
  opacity: 0.01;
  transform: translate(0, 1em);
}
.list-item-enter.list-item-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-out, transform 500ms ease-out;
  transform: translate(0, 0);
}
.list-item-leave {
  opacity: 1;
}
.list-item-leave.list-item-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in, transform 300ms ease-in;
  transform: translate(5em, 0) scale(0, 0);
}
